@color-primary: @color-brand-primary;

@color-border-dark: @color-primary;
@color-border-light: @color-gray-light;

@color-highlight-text: @color-primary;

@color-hover-text: @color-gray;
@color-hover-bg: @color-gray-lighter;

@color-left-column: @color-gray;
@color-left-column-bg: @color-gray-lighter;

@color-right-column-light: @color-gray;
@color-right-column-dark: @color-gray-dark;
@color-right-column-bg: @color-gray-lightest;

@breakpoint-medium: 768px;
@breakpoint-wide: 992px;

@search-width-medium: 250px;
@search-width-wide: 350px;

/**
 * Docsearch field
 */
.docsearch {
    float: right;
    margin-left: 20px;
    position: relative;
}

.docsearch__input {
    border: 1px solid @color-gray-light;
    color: @color-primary;
    display: inline-block;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.3;
    margin-top: 2px;
    padding: 0.45em 2.1em 0.48em 0.8em;
    width: @search-width-medium;
}

.docsearch__input:focus {
    border: 1px solid @color-border-dark;
    outline: none;
}

.docsearch__icon {
    display: block;
    height: 32px;
    opacity: .3;
    position: absolute;
    right: 3px;
    top: 4px;
    width: 32px;
    z-index: 10;
    background-image: url('data:image/png;base64,');
}

/**
 * Search result elements that are added by Algolia's script
 */

/* Search results dropdown */
.aa-dropdown-menu {
    background-color: @color-right-column-bg;
    border: 1px solid @color-border-dark;
    border-top: none;
    font-size: .85rem;
    line-height: 1.3;
    margin: 0;
    text-align: left;
    width: @search-width-medium;
}

/* Each result */
.algolia-docsearch-suggestion {
    color: @color-right-column-light;
    cursor: pointer;
    overflow: hidden;
    border-top: 1px solid @color-border-light;

    .aa-suggestion:first-of-type & {
        border-top: none;
    }
}

.algolia-docsearch-suggestion--content {
    border-top: 1px solid @color-border-light;
    padding: 10px 13px 7px 13px;
    width: 100%;

    .algolia-docsearch-suggestion__main &,
    .algolia-docsearch-suggestion__secondary &, {
        border-top: 0;
    }
}

/* Page title is displayed inline on small/medium screens
   and in a column on wide screens */
.algolia-docsearch-suggestion--subcategory-inline {
    display: block;
    font-size: .75em;
    margin-bottom: 1px;
}

.algolia-docsearch-suggestion--subcategory-column {
    cursor: default;
    display: none;
}

/* Result title */
.algolia-docsearch-suggestion--title {
    color: @color-right-column-dark;
    font-weight: 600;
    margin: 0 0 2px 0;
}

/* Result text snippet, hidden on small screens */
.algolia-docsearch-suggestion--text {
    display: none;
}

/* Text from search box is highlighed inline in each result */
.algolia-docsearch-suggestion--highlight {
    margin: 0;
    color: @color-highlight-text;
    background: none;
    font-weight: 600;
}

/* Selected result */
.aa-cursor .algolia-docsearch-suggestion--content {
    color: @color-hover-text;
    background: @color-hover-bg;
}

.aa-cursor .algolia-docsearch-suggestion--title {
    color: @color-primary;
}

/* Main category headers, unused */
.algolia-docsearch-suggestion--category-header {
    display: none;
}

/* Footer with Algolia logo*/
.algolia-docsearch-footer {
    border-top: 1px solid @color-border-light;
    text-align: right;
    font-size: .8em;
    padding: 4px 8px 3px 0;
    color: lighten(@color-gray, 5%);
    .algolia-docsearch-footer--logo {
        display: inline-block !important;
        width: 45px;
        height: 16px;
        text-indent: 101%;
        overflow: hidden;
        white-space: nowrap;
        background-image: url('data:image/png;base64,');
        background-repeat: no-repeat;
        background-size: contain;
        vertical-align: middle;
    }
}

/* Medium breakpoint: Screen is big enough to display the text snippets */
@media (min-width: @breakpoint-medium) {
    .algolia-docsearch-suggestion--content {
        display: table-cell;
        margin: 6px 10px 3px 10px;
    }

    .algolia-docsearch-suggestion--title {
        margin: 0;
    }

    .algolia-docsearch-suggestion--text {
        display: block;
        font-size: .92em;
        margin: 2px 5px 4px 14px;
    }
}

/* Wide breakpoint: Screen is big enough to display results in two columns */
@media (min-width: @breakpoint-wide) {
    .aa-input,
    .aa-dropdown-menu {
        width: @search-width-wide;
    }

    .algolia-docsearch-suggestion {
        display: table;
        width: 100%;
    }

    .algolia-docsearch-suggestion--subcategory-inline {
        display: none;
    }

    .algolia-docsearch-suggestion--subcategory-column {
        background: @color-left-column-bg;
        border-right: 1px solid @color-border-light;
        color: @color-left-column;
        display: table-cell;
        font-size: .8em;
        max-width: 115px;
        min-width: 115px;
        overflow: hidden;
        text-align: right;
        text-overflow: ellipsis;
        vertical-align: top;
        width: 115px;
    }

    .algolia-docsearch-suggestion--subcategory-column-text {
        display: none;

        .algolia-docsearch-suggestion__secondary & {
            display: block;
            margin: 12px 8px 5px 5px;
        }
    }

    .algolia-docsearch-suggestion--text {
        margin: 2px 0 3px 10px;
    }

    .aa-cursor .algolia-docsearch-suggestion--subcategory-column {
        background: darken(@color-hover-bg, 3%);
        cursor: pointer;
    }
}
